<template>
  <div class="mt-10">
    <form
      class="w-full max-w-screen-sm mx-auto"
      @submit.prevent="handleSubmit"
      v-if="defaultKey === 'form'"
    >
      <FormInput
        placeholder="請輸入現在的密碼"
        :error="currentPasswordError"
        :value="currentPassword"
        @onChange="handleChangeCurrentPassword"
        type="password"
      />
      <FormInput
        placeholder="請輸入密碼（6-20英數字,不含特殊符號）"
        :error="passwordError"
        :value="password"
        @onChange="handleChangePassword"
        type="password"
      />
      <FormInput
        placeholder="再次輸入密碼（6-20英數字,不含特殊符號）"
        :error="repeatPasswordError"
        :value="repeatPassword"
        @onChange="handleChangeRepeatPassword"
        type="password"
      />
      <button
        class="rounded w-full py-2.5 lg:py-4 text-center text-white bg-button hover:bg-main-red text-xl lg:text-3xl"
      >
        確定送出
      </button>
    </form>
    <div class="flex justify-center mt-5 lg:mt-40" v-else>
      <div class="flex items-center gap-8 lg:text-4xl flex-col lg:flex-row">
        <img src="@/assets/success.png" class="w-20 h-20" />
        修改完成，請重新登入
      </div>
    </div>
  </div>
</template>
<script>
import FormInput from "@/components/FormInput";

export default {
  components: {
    FormInput,
  },
  data() {
    return {
      defaultKey: "form",
      currentPassword: "",
      currentPasswordError: "",
      password: "",
      passwordError: "",
      repeatPassword: "",
      repeatPasswordError: "",
    };
  },
  methods: {
    handleChangeCurrentPassword(value) {
      this.currentPassword = value;
    },
    handleChangePassword(value) {
      this.password = value;
    },
    handleChangeRepeatPassword(value) {
      this.repeatPassword = value;
    },
    handleSubmit() {
      if (!this.currentPassword) {
        this.currentPasswordError =
          "*請使用6-20英數字,不含特殊符號/與現有的密碼不符";
        return;
      }
      if (!this.password) {
        this.passwordError = "*請使用6-20英數字,不含特殊符號";
        return;
      }
      if (this.password !== this.repeatPassword) {
        this.repeatPasswordError = "*與前次密碼不符";
        return;
      }
      console.log(this.password);
      this.defaultKey = "success";
    },
  },
};
</script>
