<template>
  <div class="mb-6">
    <input
      class="px-2.5 py-4 lg:p-5 w-full border-1 border-solid border-input text-base lg:text-2xl rounded-md"
      :placeholder="placeholder"
      :value="value"
      @input="handleChange"
      :type="type"
    />
    <div class="flex items-center justify-between mt-2.5 h-5 lg:h-7">
      <p
        class="text-main-red text-base lg:text-xl"
        :class="{
          'opacity-0': !error,
        }"
      >
        {{ error }}
      </p>
      <slot></slot>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    type: {
      type: String,
      default: "text",
    },
    value: {
      type: String,
      default: "",
    },
    placeholder: {
      type: String,
      default: "",
    },
    error: {
      type: String,
      default: "",
    },
  },
  methods: {
    handleChange(e) {
      this.$emit("onChange", e.target.value);
    },
  },
};
</script>
